/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.course_details {
  white-space: normal !important ;
  overflow: hidden;
  height: 0;
  z-index: 1;
}
.card_outer:hover .course_details_wrapper .course_details,
.course_details_wrapper:hover .course_details {
  padding: 2rem;
  height: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.course_details_wrapper::before,
.course_details_wrapper::after {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  left: -15px;
  background-color: #fff;
}
.course_details_wrapper::before {
  transform: skewX(45deg);
  top: 30%;
}
.course_details_wrapper::after {
  bottom: 70%;
  transform: skewX(-45deg);
}
.category_card:last-child .card_outer .course_details_wrapper::before,
.category_card:last-child .card_outer .course_details_wrapper::after,
.category_card_details:nth-child(4) .card_outer .course_details_wrapper::before,
.category_card_details:nth-child(4) .card_outer .course_details_wrapper::after ,
.category_card_details:nth-child(8) .card_outer .course_details_wrapper::before,
.category_card_details:nth-child(8) .card_outer .course_details_wrapper::after {
  right: -15px;
  left: auto;
}

.category_card:last-child .card_outer .course_details_wrapper::before {
  transform: skewX(-45deg);
}
.category_card:last-child .card_outer .course_details_wrapper::after {
  transform: skewX(45deg);
}
.category_card_details:nth-child(4) .card_outer .course_details_wrapper::before ,
.category_card_details:nth-child(8) .card_outer .course_details_wrapper::before {
  transform: skewX(-45deg);
}
.category_card_details:nth-child(4) .card_outer .course_details_wrapper::after ,
.category_card_details:nth-child(8) .card_outer .course_details_wrapper::after {
  transform: skewX(45deg);
}
