  color: #000;
}
.pagination_profile::after {
  content: "";
  position: absolute;
  transition: all 0.5s;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ff3000;
}
.pagination_profile:hover::after {
  width: 80%;
}
.pagination_profile.page_active::after {
