.course_details_wrapper {
  position: absolute;
  top: 0;
  height: 0;
  overflow: hidden;
  width: 350px;
  z-index: 10;
  background-color: #fff;
  transition: all 0.3s;
  box-sizing: border-box;
  will-change: height;
}
.addcoursebtn {
  width: 50%;
}
.course_details_wrapper:hover,
.card_outer:hover .course_details_wrapper {
  height: max-content;
  overflow: visible;
}
@media (max-width: 1000px) {
